<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

li {
    display: inline-block;
    margin: 0 0.625rem /* 10/16 */;
}
el-dropdown {
    vertical-align: top;
}

.aboutHeader {
    background: rgba(1, 1, 1, 0.7);
    height: 3.5rem /* 70/16 */ !important;
    width: 100%;
    line-height: 3.5rem /* 70/16 */;
    position: fixed;
    top: 0;
    z-index: 9998;
}
.el-main {
    background: greenyellow;
}
.el-footer {
    background: #646466;
    height: auto!important;
}
</style>



