<template>
  <el-container>
    <el-header>
      <Header></Header>
    </el-header>
    <el-main>Serve</el-main>
    <el-footer><Footer></Footer></el-footer>
  </el-container>
</template>

<script>
import Header from "@/common/header.vue";
import Footer from "@/common/footer.vue";
export default {
    components: {
        Header,
        Footer
    },
    data() {
         return {
            
         }
    },
    methods: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}
ul {
    list-style-type: none;
    padding: 0;
}
li {
    display: inline-block;
    margin: 0 10px;
}
a {
    color: #42b983;
}
el-dropdown {
    vertical-align: top;
}
.el-header {
    background: rgba(1, 1, 1, 0.7);
    height: 3.5rem /* 70/16 */ !important;
    width: 100%;
    line-height: 3.5rem /* 70/16 */;
    position: fixed;
    top: 0;
    z-index: 9998;      
}
.el-main {
    background: greenyellow;
}
.el-footer {
    background: #646466;
    height: auto!important;
}
</style>



