<template>
  <div class="headerBox">
    <div class="logoBox"><a href="#">{{title}}</a></div>
    <ul class="navList">
      <div @click="openFullScreen2"><router-link to="/home">首页</router-link></div>
      <div @click="openFullScreen2"><router-link to="/about" >关于</router-link></div>
      <div @click="openFullScreen2"><router-link to="/serve">服务</router-link></div>
      <div @click="openFullScreen2"><router-link to="/example">案例</router-link></div>
      <div @click="openFullScreen2"><router-link to="/trends">动态</router-link></div>
      <div @click="openFullScreen2"><router-link to="/connection">联系</router-link></div>
      <div class="photoNum" @click="openFullScreen2">{{tel}}</div>
    </ul> 
  </div>
</template>
<script>
import 'element-ui/lib/theme-chalk/display.css';
export default {
    data() {
      return{
        title:"齐艺科技",
        tel:"027-8360771",
        fullscreenLoading: false
      }
    },
    methods:{
          openFullScreen2() {
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
        }, 1000);
      }
    }
};
</script>
<style scoped>
.headerBox{
  overflow: hidden;
  font-family: "Microsoft YaHei","微软雅黑",Arial;
}
.logoBox{
  float: left;
}
.logoBox a{
  font-size: 11px;
  color:aliceblue;
}
.photoNum{
  display: inline-block;
  color:aliceblue;
  padding: 0 20px;
  font-size: 7px ;
}
.navList{
  float: right;
}
.navList div{
  float: left;
}
.navList a{
  font-size: 7px ;
  color: aliceblue;
  display: inline-block;
  padding: 0 20px;
}

</style>




